    filter: drop-shadow(0 5px 5px rgba(0, 0, 0, 0.1));
    transition: all 0.5s ease;
    border: none;
    background: #ffd800;
    border-radius: 2px;
    text-transform: uppercase;
    box-sizing: border-box;
    padding: 10px 20px;
    font-family: "lora", serif;

    font-size: 1em;
    cursor: cursor;
    text-decoration: none;
    color: #262626;
  }
}

.slider-content .inner a:hover {
  color: #262626;
  background: #ffe136;
  -webkit-filter: drop-shadow(0 0 5px rgba(0, 0, 0, 0.2));
  filter: drop-shadow(0 0 5px rgba(0, 0, 0, 0.2));
}

.wrapper {
  max-width: 1335px;
  box-sizing: border-box;
  padding: 0 20px;
  margin: auto;
}

.wrapper a {
  display: inline-block;
  margin: 5px;
}

.wrapper a:first-child {
  margin-left: 0;
}

.slider-wrapper {
  position: relative;
  height: 80vh;
  overflow: hidden;
}

.slide {
  height: 80vh;
  background-size: cover !important;
}

.slide::before {
  content: "";
  display: block;
  position: absolute;
  width: 100%;
  height: 100%;
  background: linear-gradient(to right, #77777724 30%, rgb(41, 53, 43));
  bottom: 0;
  left: 0;
}

.previousButton,
.nextButton {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  z-index: 10;
  background: url(/index.html/static/media/next.b57c7f96.svg) no-repeat center center / 16px;
  width: 52px;
  height: 52px;
  padding: 10px;

  text-indent: -9999px;
  cursor: pointer;
}

.previousButton:hover,
.nextButton:hover {
  background: url(/index.html/static/media/next.b57c7f96.svg) no-repeat center center / 16px;
}

.previousButton {
  left: 0;
  transform: rotate(180deg) translateY(calc(50% + 0px));
}

.previousButton:hover {
  left: -10px;
}

.nextButton {
  right: 0;
}

.nextButton:hover {
  right: -10px;
}

.slider-content {
  text-align: right;
}
@media (max-width: 500px) {
  .slide {
    background-position-x: 30% !important;
  }
}

.slider-content .inner {
  padding: 0 70px;
  box-sizing: border-box;
  position: absolute;
  width: 70%;
  top: 50%;
  left: 60%;
  transform: translate(-50%, -50%);
}

.slider-content .inner button {
  transition-delay: 0s;
}
.slider-content .inner a {
  transition-delay: 0s;
}

.slider-content .inner h1 {
  font-weight: 900;
  margin: 0 auto;
  max-width: 840px;
  color: #ffffff;
  font-size: 64px;
  line-height: 1;
  margin-right: 20px;
}

.slider-content .inner p {
  color: #ffffff;
  font-size: 1.6em;
  line-height: 1.5;
  margin: 20px;
  font-family: "source sans pro", sans-serif;
  margin-left: auto;
  max-width: 640px;
}
@media (max-width: 500px) {
  .slider-content .inner p {
    color: #ffffff;
    font-size: 1.2em;
